export default function MovieSkeleton() {
  return (
    <div>
      {/* Backdrop skeleton */}
      <div className="relative h-[500px] bg-gray-800 animate-pulse">
        <div className="absolute inset-0 bg-gradient-to-r from-black/90 via-black/70 to-black/20" />

        <div className="absolute inset-0 flex justify-center">
          <div className="w-full max-w-6xl px-6 pt-10 flex gap-10">
            {/* Poster */}
            <div className="w-[300px] h-[450px] bg-gray-700 rounded-xl" />

            {/* Details */}
            <div className="flex-1 space-y-4">
              <div className="h-8 w-2/3 bg-gray-700 rounded" />
              <div className="h-4 w-1/2 bg-gray-700 rounded" />

              <div className="flex items-center gap-6 mt-6">
                <div className="w-16 h-16 bg-gray-700 rounded-full" />
                <div className="h-10 w-40 bg-gray-700 rounded-full" />
              </div>

              <div className="flex gap-4 mt-6">
                {[1, 2, 3].map((i) => (
                  <div key={i} className="w-10 h-10 bg-gray-700 rounded-full" />
                ))}
                <div className="h-6 w-32 bg-gray-700 rounded" />
              </div>

              <div className="space-y-2 mt-6">
                <div className="h-4 w-full bg-gray-700 rounded" />
                <div className="h-4 w-full bg-gray-700 rounded" />
                <div className="h-4 w-3/4 bg-gray-700 rounded" />
              </div>

              <div className="grid grid-cols-2 gap-6 mt-8">
                {[1, 2, 3, 4].map((i) => (
                  <div key={i}>
                    <div className="h-4 w-32 bg-gray-700 rounded" />
                    <div className="h-3 w-20 bg-gray-700 rounded mt-1" />
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Cast skeleton */}
      <div className="max-w-6xl mx-auto px-6 mt-12">
        <div className="h-6 w-48 bg-gray-300 rounded mb-6 animate-pulse" />

        <div className="grid grid-cols-6 gap-6">
          {Array.from({ length: 6 }).map((_, i) => (
            <div key={i} className="animate-pulse">
              <div className="h-[280px] bg-gray-300 rounded-lg" />
              <div className="h-4 bg-gray-300 rounded mt-2" />
              <div className="h-3 bg-gray-200 rounded mt-1 w-3/4" />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
